
//UNDER 768PX
@media (max-width: 768px) {

    //FOOTERS partner and client footers hidden for smaller screens. //DONE
    .client-footer,
    .partner-footer {
        display: none;
    }
  }


//FROM 768PX
@media (min-width: 768px) {

    //NAV SEARCH BAR moves up- not accurate, testing
    #search-bar {
        width: 15%;
        margin-left: 20px;
    }

    #menu {
        order: 2;
        margin-left: 20px;
    }

    //FOOTERS main footer splits into 2 columns. TO EDIT MARGINS/PADDING
    .main-footer {
        flex-direction: row; //started working on 2 and 4 column layouts for larger screens
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-around;
    }

    .about,
    .contact,
    .website,
    .social {
        width: 50%;
        border: 2px solid red;
        align-self: flex-start;
    }

    //LATEST 3rd news item hidden. //DONE
    .news-3 {
        display: none;
    }

    //NAV call button disappears. //DONE
    #direct-call {
        display: none;
    }

    //CARD bottom 4 cards switch to 2 column layout. TO LOOK AT MARGINS/PADDING.
    .cards {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .card-telecoms,
    .card-web,
    .card-cyber,
    .card-training {
        width: 45%;
    }

    //LATEST switch to 2 column layout. Fixed width. 
    .latest {
        flex-direction: row;
        flex-wrap: wrap;
    }

    .latest h2 {
        display: block;
        width: 100%;
    }
    .news {
        width: 345px; //was 45%
    }
}




//INTRO max width activated
//INTRO left margin starts to expand
