//FROM 992PX


@media (min-width: 992px) {

        //NAV bar appears. //DONE
        .nav-2 {
            display: block;
        }

        //INTRO background image added. //DONE
        .intro {
            background: #333645 url("../../html-css-reflection/images/internal-branding/logo-zoomed-transparent.png") no-repeat right top;
        }

        //BANNER text aligns left & image gradient changes
        //BANNER margin-left starts expanding
        .banner {
            align-items: flex-start;
            text-align: left;
            background:  linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.4042659300048145) 35%, rgba(0,0,0,0) 100%),
                        url("../../html-css-reflection/images/slides/slide-1.jpeg");
            background-size: cover;
            padding-left: 4%; //was 15px. CAN I USE A MIN-MAX?
        }

        //CARD format 3 + 2 + 2. TO LOOK AT MARGINS/PADDING.
        .card-software,
        .card-it,
        .card-marketing {
            width: 30%;
        }

        //LATEST news items new width. TO EDIT MARGINS/SPACING
        .news {
            width: 455px;
        }

        //NAV support and contact buttons appear
        #support,
        #contact {
            display: inline-block;
        }

        #support {
            margin-left: auto;
        }
    }



//NAV search bar becomes a button that animates on focus
