//FROM 1260PX
@media (min-width: 1260px) {  

    //FOOTER main footer switches to 4 column layout. //TO EDIT MARGINS/PADDING
    .about,
    .contact,
    .website,
    .social {
        width: 25%;
    }

    // CARDS format 3 + 4. TO LOOK AT MARGINS/PADDING.
    .card-telecoms,
    .card-web,
    .card-cyber,
    .card-training {
        width: 23%;
    }

    //LATEST 3rd news item added again. //DONE.
    .news-3 {
        display: inline-block; 
    }

    //LATEST news items resized for 3 columns
    .news {
        width: 31%;
    }

    //BANNER margin left fixed width
    .banner {
        padding-left: 40px;
    }

    
}
  


